.offers{
    width: 65%;
    height: 60vh;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22, 60%);
}

.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.offers-left h1{
    color: #171717;
    font-size: 80px;
    font-weight: 600;
}

.offers-left p{
    color: #171717;
    font-size: 22px;
    font-weight: 600;
}

.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background: #ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}

.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}

/* ðŸ“Œ Responsive Design */
@media screen and (max-width: 1024px) {
    .offers {
        flex-direction: column; /* Stack elements vertically */
        text-align: center;
        padding: 40px 20px;
    }

    .offers-left h1 {
        font-size: 50px;
    }

    .offers-left p {
        font-size: 18px;
    }

    .offers-left button {
        width: 220px;
        height: 55px;
        font-size: 18px;
        margin-left: 20vw;
    }

    .offers-right {
        justify-content: center;
        padding-top: 30px;
    }
}

@media screen and (max-width: 768px) {
    .offers-left h1 {
        font-size: 40px;
    }

    .offers-left p {
        font-size: 16px;
    }

    .offers-left button {
        width: 200px;
        height: 50px;
        font-size: 16px;
        margin-left: 15vw;

    }
}

@media screen and (max-width: 480px) {
    .offers-left h1 {
        font-size: 30px;
    }

    .offers-left p {
        font-size: 14px;
    }

    .offers-left button {
        width: 180px;
        height: 45px;
        font-size: 14px;
        margin-left: 10vw;
    }

    .offers-right img {
        width: 80%;
    }
}