.relatedproducts{
    display: flex;
   flex-direction: column;
   align-items: center;
   gap: 20px;
   height: auto;
   padding: 20px;
}

.relatedproducts h1{
    font-size: 50px;
    font-weight: 600;
    color: #171717;
}

.relatedproducts hr{
    width:150px;
    height:6px;
    border-radius: 10px;
    background: #252525;
}

.relatedproducts-item {
    display: grid;
    grid-template-columns: repeat(4, 1fr); 
    gap: 15px; 
    justify-content: center;
    width: 100%;
    max-width: 1200px;
